<template>
  <div>
    <h1 class="text-center">ZoHo Admin Panel</h1>
  </div>
</template>

<script>
export default {
  name: 'Dashboard',
  components: {

  },
  data () {
    return {
      selected: 'Month',
    }
  },
  created(){
    this.getCode();
  },
  methods: {
    color (value) {
      let $color
      if (value <= 25) {
        $color = 'info'
      } else if (value > 25 && value <= 50) {
        $color = 'success'
      } else if (value > 50 && value <= 75) {
        $color = 'warning'
      } else if (value > 75 && value <= 100) {
        $color = 'danger'
      }
      return $color
    },
    getCode(){
      var self = this;
      fetch('https://accounts.zoho.com/oauth/v2/auth?scope=ZohoBooks.invoices.CREATE,ZohoBooks.invoices.READ,ZohoBooks.invoices.UPDATE,ZohoBooks.invoices.DELETE&client_id=1000.922G98M52JVPYFLRSWE1XTEQ5AFT6X&state=testing&response_type=code&redirect_uri=http://zoho.codewithbogo.in/dashboard&access_type=online',
      { mode: 'no-cors' }
      )
      .then(function(response) {
        if (response.status !== 200) {
          console.log(
            "Looks like there was a problem. Status Code: " + response.status
          );
          return;
        }
        response.json().then(function(data) {
          console.log(data)
        });
      })
      .catch(function(err) {
        console.log("Fetch Error :-S", err);
      });
    },
  }
}
</script>
